/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { BlockControls, InspectorControls, useBlockProps } from '@wordpress/block-editor';
import { ToolbarGroup, Placeholder, TextControl, Button } from '@wordpress/components';
import { useState } from '@wordpress/element';
import { edit } from '@wordpress/icons';
import ServerSideRender from '@wordpress/server-side-render';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import TheSidebar from './_sidebar';
import arkheIcon from '@src/gutenberg/arkhe-icon';
import { ArkbMarginControl } from '@components';

/**
 * Register Block
 */
const blockName = 'ark-block-rss';
registerBlockType(metadata.name, {
	title: __('RSS list', 'arkhe-blocks'),
	description: __('Create a list of RSS feeds.', 'arkhe-blocks'),
	icon: (
		<svg viewBox='0 0 24 24'>
			<path d='M5 10.2h-.8v1.5H5c1.9 0 3.8.8 5.1 2.1 1.4 1.4 2.1 3.2 2.1 5.1v.8h1.5V19c0-2.3-.9-4.5-2.6-6.2-1.6-1.6-3.8-2.6-6.1-2.6zm10.4-1.6C12.6 5.8 8.9 4.2 5 4.2h-.8v1.5H5c3.5 0 6.9 1.4 9.4 3.9s3.9 5.8 3.9 9.4v.8h1.5V19c0-3.9-1.6-7.6-4.4-10.4zM4 20h3v-3H4v3z'></path>
		</svg>
	),
	edit: ({ attributes, setAttributes }) => {
		const { rssUrl } = attributes;
		const [isEditing, setIsEditing] = useState(!rssUrl);

		const blockProps = useBlockProps({
			className: blockName,
		});

		function onSubmitURL(event) {
			event.preventDefault();

			if (rssUrl) {
				setIsEditing(false);
			}
		}

		if (isEditing) {
			return (
				<div {...blockProps}>
					<Placeholder label='RSS' icon={arkheIcon}>
						<form onSubmit={onSubmitURL} className='ark-block-rss__placeholder-form'>
							<TextControl
								placeholder={__('Enter URL here…')}
								value={rssUrl}
								onChange={(value) => setAttributes({ rssUrl: value })}
								className='ark-block-rss__placeholder-input'
							/>
							<Button variant='primary' type='submit'>
								{__('Use URL')}
							</Button>
						</form>
					</Placeholder>
				</div>
			);
		}

		const toolbarControls = [
			{
				icon: edit,
				title: __('Edit RSS URL'),
				onClick: () => setIsEditing(true),
			},
		];

		return (
			<>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
					<ToolbarGroup controls={toolbarControls} />
				</BlockControls>
				<InspectorControls>
					<TheSidebar {...{ attributes, setAttributes }} />
				</InspectorControls>
				<div {...blockProps}>
					<ServerSideRender block={metadata.name} attributes={attributes} />
				</div>
			</>
		);
	},
	save: () => {
		return null;
	},
});
